import axios from 'axios';
import React, { Fragment, useEffect, useState } from 'react'
import environment from '../../../environment';
import "./Gallery.css"
function Images() {
  const [Datas, setData] = useState([{}]);
   
    
  let getUrl = `${environment.baseUrl}/api/gallery/all`;
   useEffect(()=>{
       axios.get(getUrl).then(resp=>{
           console.log("got data", resp)
           if(resp.data.success){
           setData(resp.data.data);
           }
       }).catch(e=>{ console.log("Error ", e)})
     },[]
   )


  return (
    <>
            <div className='container-gallery-content row justify-content-center align-items-center m-5 p-4'>
            {Datas && Datas.map((data)=>{
                return(
                    <Fragment>
                    <div key={data._id} className="p-3 card mt-3 mr-1" style={{width: '350px', height:"35vw", minHeight:"330px"}}>
                      <div className="d-flex flex-column justify-content-between">
                        <img src={`../upload/${data.imgUrl}`} height='100%' alt={"Image"}/>
                        <div className='w-25 d-flex flex-column justify-content-center align-items-center'>
                        </div>
                       
                      </div>
                    </div>
                    </Fragment>
                )
            })}


            </div>
    </>
  )}
  export default Images