import React, { Fragment } from 'react'

const GalleryCard = ({data, del, edit}) => {
  return (
    <Fragment>
  <div key={data._id} className="p-3 card mt-3 mr-1" style={{width: '350px', height:"400px"}}>
    <div className="d-flex flex-column justify-content-between">
      <img src={`../upload/${data.imgUrl}`} height='100%' alt={"Image"}/>
      <div className='w-25 d-flex flex-column justify-content-center align-items-center'>
      {/* <button className="btn btn-edit btn-sm btn-info" onClick={()=>{edit(data._id)}}>Edit</button> */}
      <button className="btn btn-delete btn-sm btn-danger" onClick={()=>{del(data._id)}}>Delete</button>
      </div>
     
    </div>
  </div>
  </Fragment>
  )
}

export default GalleryCard