import React, { Fragment } from 'react';
import MetaTags from 'react-meta-tags';
import RegistrationForm from '../containers/registration/registration-form/RegistrationForm';
import Breadcrumb from "../components/UI/breadcrumb";
import LayoutOne from '../layouts';
import Images from '../containers/gallery/Images/Images';

const GalleryPage = () => {
  return (
    <Fragment>
      <MetaTags>
        <title>Gallery</title>
        <meta name='description' content='Registrationpage of event management service.' />
      </MetaTags>
      <LayoutOne footerBg='white'>
      <Breadcrumb title="Gallery" />
      <div style={{marginBottom: '30px'}}></div>
        <Images/>
      </LayoutOne>
    </Fragment>
  );
};

export default GalleryPage;
