import React from 'react';
import { Link } from 'react-router-dom';

const Logo = ({logo}) => {
  return (
    <div className='logo'>
      <Link to={ '/'}>
        {/* <h1 className='text-white'>LOGO</h1> */}
        <img style={{ width: 90, height: 90 }} alt='Logo' src={`../upload/${logo?.imgUrl}`}  />
      </Link>
    </div>
  );
};

export default Logo;
